<template>
    <div class='news_list'>
        <div class="header">
            <div class="back"  @click.stop="back()">
                <img src="../../assets/image/联合42.png" alt="">
            </div>
            <div class="news_list_head">新闻</div>
            <div class='search' @click='enterSearch()'>
                <img src="../../assets/image/search.png" alt="">
            </div>
        </div>
        <v-page class="plan-list-page" style="font-size: 0.3rem;">
            <mt-loadmore ref="loadmore" class="loadmore"
                        :top-method="loadTop" v-infinite-scroll="loadBottom" infinite-scroll-disabled="moreLoading01" infinite-scroll-distance="40" infinite-scroll-immediate-check="false" :autoFill="false">
            <div class="activity-list" ref="scrollContainer">
                <div  v-for='(item) in newsListData' :key="item.id" class='newsList_item' @click.stop='enterNewsDetail(item.id)'>
                    <div class='newsListcontainLeft'>
                        <img src="../../assets/image/新闻图.png" alt="" v-if='!item.newsImg'>
                        <img :src="item.newsImg" alt="" v-if='item.newsImg'>
                    </div>
                    <div class='newsListcontainRight'>
                        <div class='containTop'>
                            {{item.title}}
                        </div>
                        <div class='containBottom'>
                            <div class='containSource'>
                                {{item.source}}
                            </div>
                            <div class='containTime'>
                                {{item.publishTime}}
                            </div>
                        </div>
                    </div>
                </div>
                <div class="more_loading" style="height:0.30rem; line-height:0.30rem; text-align:center" v-show="!queryLoading01">
                    <div type="snake" color="#00ccff" :size="20" v-show="moreLoading01 && !allLoaded01">加载中...</div>
                    <div v-show="allLoaded01" style="color:#ccc;margin-top:0.1rem;">---- 看到底线了 ----</div>
                </div>
            </div>
            </mt-loadmore>
            <div class="list-empty-show" v-if="isEmpty">
                暂无数据
            </div>
        </v-page>
        <div class='empty'>
            <img src="../../assets/image/空空如也.png" alt="">
            <div class='emptyWord'>空空如也~</div>
        </div>
         <!-- 加载页面 -->
        <Loading :dialogVisible="verDialogVisible"></Loading>
    </div>
</template>

<script>
import 'wangeditor/release/wangEditor.min.css'
import Loading from './../components/loading'
import API from '@/api/api'
import $ from 'jquery'
import { Indicator } from 'mint-ui'

export default {
  name: 'index',
  components: {
    Loading
  },
  data () {
    return {
      name: '',
      newsListData: [],
      isEmpty: false, // 列表查询结果是否为空
      allLoaded: false, // 列表数据是否全部加载
      topStatus: false, // 顶部下拉刷新
      queryLoading01: false,
      moreLoading01: false,
      allLoaded01: false,
      pageSize: 10,
      pageNum: 1,
      pageCount: 100,
      verDialogVisible: true
    }
  },
  created () {
    //   this.name = localStorage.getItem("dataListName");
    //   console.log(.getItem("dataListSpecialCode"))
    this.getfindNewsBase('down')
  },
  mounted () {
    //   this.returnButton();
  },
  methods: {
    // 下拉刷新
    loadTop () {
      this.pageNum = 1
      this.allLoaded = false
      Indicator.open({
        text: '加载中...',
        spinnerType: 'fading-circle'
      })
      this.getfindNewsBase('down')
      this.$refs.loadmore.onTopLoaded()
    },
    // 上拉加载
    loadBottom () {
      if (this.allLoaded01) {
        this.moreLoading01 = true
        return
      }
      if (this.queryLoading01) {
        return
      }
      this.moreLoading01 = !this.queryLoading01
      setTimeout(() => {
        this.pageNum++
        this.getfindNewsBase('up')
      }, 500)
    },
    // 获取新闻
    getfindNewsBase (flag) {
      this.queryLoading01 = false
      this.moreLoading01 = false
      this.allLoaded01 = false
      const that = this
      API.post('findNewsBase', {
        pageMsg: {
          'currentPage': this.pageNum,
          'pageSize': this.pageSize },
        queryFilter: {
          'status': '1302'
        }
      }, 'json', (data, err) => {
        if (data.success) {
          if (flag === 'down') {
            this.newsListData = []
          }
          // console.log(data)
          this.pageCount = data.data.total
          that.forBackData(data.data.records)
          that.verDialogVisible = false
          // this.newsListData = data.data.pageData
        } else {
          console.log(data.msg)
          that.verDialogVisible = false
        }
      })
    },
    // 循环返回的数据
    forBackData (list) {
      for (var i = 0; i < list.length; i++) {
        list[i].publishTime = this.GLOBAL.changeTime(list[i].publishTime)
        this.newsListData.push(list[i])
      }
      // this.pageCount = this.newsListData.length;
      // console.log(this.newsListData)
      this.isEmpty = false
      this.allLoaded01 = this.newsListData.length === this.pageCount || list.length === 1
      // console.log(this.newsListData.length);
      this.moreLoading01 = this.allLoaded01
      Indicator.close()
      if (this.newsListData.length === 0) {
        $('.empty').css('display', 'flex')
        $('.plan-list-page').css('display', 'none')
      } else {
        $('.empty').css('display', 'none')
        $('.plan-list-page').css('display', 'block')
      }
    },
    // 返回
    back () {
      this.$router.back()
    },
    // 进入搜索
    enterSearch () {
      localStorage.setItem('enterIndex', this.GLOBAL.ENTERSOURCE.newsSearch)
      this.$router.push({
        name: 'search'
      })
    },
    // 进入新闻
    enterNewsDetail (id) {
      API.post('findNewsDetilById', {
        id: id,
        status: '1302'
      }, 'json', (data, err) => {
        if (data.success) {
          if (data.data) {
            this.$router.push({
              name: 'news_detail',
              params: { id: id }
            })
          } else {
            this.$toast('该新闻不存在')
            this.loadTop()
          }
        }
      })
    }
  }
}
</script>
<style scoped>
    .news_list{
        width:100%;
        height:100%;
        background:#fff;
    }
    .news_list .header{
        width:100%;
        background:#fff;
        font-size:0.36rem;
        font-family: PingFang-SC-Medium;
        position: fixed;
        top:0;
        height: 0.94rem;
        display: flex;
        justify-content: center;
        z-index: 99;
    }
    .news_list .back{
        width:0.89rem;
        height:0.94rem;
        position: fixed;
        top:0;
        left:0;
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 100;
    }
    .news_list .back img{
        width:0.19rem;
        height:0.34rem;
    }
    .news_list .search{
        width:1.11rem;
        height: 0.94rem;
        position: fixed;
        top:0;
        right:0;
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 99;
    }
    .news_list .search img{
        width:0.41rem;
        height:0.41rem;
    }
    .news_list_head{
        line-height: 0.94rem;
        font-size:0.38rem;
        color:#4E4E4E;
        width:3rem;
        overflow-x:hidden;
        text-overflow:ellipsis;
        padding-top:0.02rem;
        white-space:nowrap;
        text-align: center
    }
    .activity-list{
        background: #fff;
        padding:0 0.35rem;
        /* margin-top:0.94rem; */
        width:100%;
        height:calc(100vh - 0.94rem);
    }
    .newsList_item{
        padding:0.3rem 0;
        height:2.3rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        border-top:0.012rem solid rgba(243,243,243,1);
    }
    .newsList_item:first-child{
        padding-top:0.07rem;
        height:2.07rem;
        border-top:0;
    }
    .newsListcontainLeft{
        /* margin:0.3rem 0; */
        height:1.7rem;
        width:2.63rem;
    }
    .newsListcontainLeft img{
        height:1.7rem;
        width:2.63rem;
        display: block;
        border-radius: 0.06rem;
    }
    .newsListcontainRight{
        width:3.87rem;
        height:1.7rem;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }
    .newsListcontainRight .containTop{
        color:#4E4E4E;
        font-size:0.34rem;
        width:100%;
        line-height:0.52rem;
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        word-wrap: break-word;
        word-break: normal;
        /*! autoprefixer: off */
        /* autoprefixer: on */
    }
    .newsListcontainRight .containBottom{
        width:100%;
        display: flex;
        align-items: flex-end;
        justify-content: space-between;
        font-size:0.24rem;
        color:#9FA7AF;
        height:0.5rem;
    }
    .containBottom .containSource{
        width:2.5rem;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        padding-top:0.2rem;
        padding-bottom:0.02rem;
    }
    .containBottom .containTime{
        padding-bottom:0.02rem;
    }
    .empty{
        margin-top:2.75rem;
        display: none;
        flex-direction: column;
        width:100%;
        align-items: center;
    }
    .empty img{
        width:2.91rem;
        height:2.63rem;
    }
    .empty .emptyWord{
        margin-top:0.4rem;
        color:#9FA7AF;
        font-size:0.32rem;
    }
    .mint-loadmore{
        max-height: calc(100vh - 0.94rem);
        overflow-y: auto;
        /* -webkit-overflow-scrolling: touch; */
        margin-top: 0.94rem;
        background: #fff;

    }
</style>



